import React, { useState, useEffect } from "react";
import "react-datepicker/dist/react-datepicker.css";
import TopBar from "../../../components/Users/UserDashboard/TopBar";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  Stepblock,
  SectionWrapper,
  Titletext,
  Wrapper,
} from "../../../components/Styles/RegisterStyles";
import ModuleSideNav from "../../../components/Users/UserDashboard/ModuleSideNav";

import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  LineElement,
  PointElement,
  ArcElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import axios from "axios";
import { useNavigate } from "react-router-dom";
ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  LineElement,
  PointElement,
  ArcElement,
  Title,
  Tooltip,
  Legend
);

export default function Dashboard() {
  const navigate = useNavigate();
  const apiUrlCompany = "https://blueprintcatalyst.com/api/user/company/";
  const apiUrlDashbaord = "https://blueprintcatalyst.com/api/user/dashboard/";
  const storedUsername = localStorage.getItem("OwnerLoginData");
  const userLogin = JSON.parse(storedUsername);
  const [companyData, setCompanyData] = useState([]);
  const [lastestactivitySignatory, setlastestactivitySignatory] = useState([]);
  const [companyId, setcompanyId] = useState("");
  const [CompanyName, setCompanyName] = useState("");
  const [OwnerData, setOwnerData] = useState("");
  const [spinners, setSpinners] = useState({});
  const [errr, seterrr] = useState(false);
  const [dangerMessage, setdangerMessage] = useState("");
  useEffect(() => {
    document.title = "Dashboard Page";
  }, []);
  useEffect(() => {
    getUserCompany();
  }, []);
  useEffect(() => {
    getUserOwnerDetail();
  }, []);
  const getUserOwnerDetail = async () => {
    const formData = {
      user_id: userLogin.id,
    };
    try {
      const resp = await axios.post(
        apiUrlCompany + "getUserOwnerDetail",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );
      if (resp.data.results.length > 0) {
        setOwnerData(resp.data.results[0]);
      }
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };
  const getUserCompany = async () => {
    const formData = {
      user_id: userLogin.id,
    };
    try {
      const resp = await axios.post(
        apiUrlCompany + "getUserCompany",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );
      console.log(resp.data.results);
      if (resp.data.results.length > 0) {
        setcompanyId(resp.data.results[0].id);
        setCompanyName(resp.data.results[0].company_name);
      }
      setCompanyData(resp.data.results);
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };
  const apiURL_Access = "https://blueprintcatalyst.com/api/user/accesslogs/";
  useEffect(() => {
    if (companyId) {
      getSignatoryActivity();
    }
  }, [companyId]);
  const getSignatoryActivity = async () => {
    const formData = {
      company_id: companyId,
    };
    try {
      const generateRes = await axios.post(
        apiURL_Access + "getCompanyLogs",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );
      console.log(generateRes.data.results);
      setlastestactivitySignatory(generateRes.data.results);
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };
  const handlegetCompanyRecord = async (corpId, comp_name) => {
    setCompanyName(comp_name);
    setcompanyId(corpId);
  };
  function formatCurrentDate(input) {
    const date = new Date(input);

    if (isNaN(date)) return "";
    const months = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ];

    const day = date.getDate();
    const month = months[date.getMonth()];
    const year = date.getFullYear();

    const getOrdinal = (n) => {
      if (n >= 11 && n <= 13) return "th";
      switch (n % 10) {
        case 1:
          return "st";
        case 2:
          return "nd";
        case 3:
          return "rd";
        default:
          return "th";
      }
    };

    return `${month} ${day}${getOrdinal(day)}, ${year}`;
  }
  const handleAccessCompany = async (id) => {
    const formData = {
      company_id: id,
      user_id: userLogin.id,
    };

    setSpinners((prev) => ({ ...prev, [id]: true })); // set spinner for this company

    setTimeout(async () => {
      try {
        const generateRes = await axios.post(
          apiUrlDashbaord + "getCompanyAccess",
          formData,
          {
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json",
            },
          }
        );
        var dataresp = generateRes.data;

        setSpinners((prev) => ({ ...prev, [id]: false })); // stop spinner for this company

        setdangerMessage(dataresp.message);
        if (dataresp.status === "1") {
          localStorage.removeItem("SignatoryLoginData");

          setTimeout(() => {
            var userData = dataresp.user;
            localStorage.setItem(
              "SignatoryLoginData",
              JSON.stringify(userData)
            );
            window.open("/dashboard", "_blank");
          }, 1500);
        } else {
          seterrr(true);
        }
        setTimeout(() => {
          seterrr(false);
          setdangerMessage("");
        }, 3500);

        console.log(generateRes.data);
      } catch (err) {
        console.error("Error generating summary", err);
        setSpinners((prev) => ({ ...prev, [id]: false })); // make sure spinner stops on error
      }
    }, 1000);
  };

  const [isCollapsed, setIsCollapsed] = useState(false);

  return (
    <>
      <Wrapper>
        <div className="fullpage d-block">
          <div className="d-flex align-items-start gap-0">
            <ModuleSideNav
              isCollapsed={isCollapsed}
              setIsCollapsed={setIsCollapsed}
            />
            <div
              className={`global_view ${isCollapsed ? "global_view_col" : ""}`}
            >
              <TopBar />
              {dangerMessage && (
                <div
                  className={`flex items-center justify-between gap-3 shadow-lg ${
                    errr ? "error_pop" : "success_pop"
                  }`}
                >
                  <div className="d-flex align-items-center gap-2">
                    <span className="d-block">{dangerMessage}</span>
                  </div>

                  <button
                    type="button"
                    className="close_btnCros"
                    onClick={() => setdangerMessage("")}
                  >
                    ×
                  </button>
                </div>
              )}
              <SectionWrapper className="d-block p-md-4 p-3">
                <div className="container-fluid">
                  <Stepblock id="step5">
                    <div className="row">
                      <div className="col-md-12">
                        <div className="pb-3 bar_design">
                          <h4 className="h5 mb-0">
                            Welcome,{" "}
                            {OwnerData.first_name || OwnerData.last_name ? (
                              <>
                                {OwnerData.first_name} {OwnerData.last_name}
                              </>
                            ) : (
                              <span className="text-muted">
                                Name not available
                              </span>
                            )}
                          </h4>
                        </div>

                        <div class="row gap-0 dashboard-top p-0 border-0 bg-transparent">
                          <div className="row gy-3 ">
                            {companyData?.map((company, index) => (
                              <button
                                key={index}
                                type="button"
                                className="col-md-4 border-0 bg-transparent"
                              >
                                <div
                                  className="card_deisgn_register"
                                  style={{
                                    borderColor:
                                      company.company_color_code || "#ccc",
                                    backgroundColor:
                                      `${company.company_color_code}50` ||
                                      "#ffffff80",
                                  }}
                                >
                                  <h5
                                    className="text-center d-flex align-items-center gap-2"
                                    style={{
                                      backgroundColor:
                                        company.company_color_code || "#000",
                                      color: "#fff",
                                      padding: "10px 20px",
                                      borderRadius: "8px",
                                      fontSize: "0.9rem",
                                    }}
                                  >
                                    <input
                                      className="checkbox_global"
                                      name="company"
                                      checked={company.id === companyId}
                                      onChange={() =>
                                        handlegetCompanyRecord(
                                          company.id,
                                          company.company_name
                                        )
                                      }
                                      type="radio"
                                    />
                                    <span className="d-block text-start">
                                      {company.company_name}
                                    </span>
                                  </h5>
                                  <p
                                    onClick={() =>
                                      handleAccessCompany(company.id)
                                    }
                                    className="py-3 text-center mb-0"
                                    style={{
                                      fontSize: "0.9rem",
                                      fontWeight: "600",
                                      position: "relative", // make parent relative
                                      cursor: "pointer",
                                    }}
                                  >
                                    Access this account.
                                    {spinners[company.id] && (
                                      <div
                                        className="spinner-border spinneronetimepay"
                                        role="status"
                                        style={{
                                          position: "absolute",
                                          top: "50%",
                                          left: "50%",
                                          transform: "translate(-50%, -50%)",
                                          width: "1rem",
                                          height: "1rem",
                                        }}
                                      >
                                        <span className="visually-hidden"></span>
                                      </div>
                                    )}
                                  </p>
                                </div>
                              </button>
                            ))}
                          </div>
                        </div>
                      </div>
                      <div className="col-12 my-4">
                        <div class="dashboard_card  modern-chart">
                          <div class="card-header">
                            <h3 class="card-title">
                              Recent Activity Investor (Round)
                            </h3>
                          </div>

                          <div class="access-logs">
                            <h4 class="section-title">
                              Comapny ({CompanyName})
                            </h4>
                            <table class="log-table">
                              <thead>
                                <tr>
                                  <th>Name</th>
                                  <th>Action</th>
                                  <th>Status</th>
                                  <th>Time</th>
                                </tr>
                              </thead>
                              <tbody>
                                {companyData.length > 0 &&
                                lastestactivitySignatory.length > 0 ? (
                                  lastestactivitySignatory.map(
                                    (activity, index) =>
                                      activity.access_status === "active" && (
                                        <tr>
                                          <td>
                                            <small>Test investor</small>
                                          </td>
                                          <td>
                                            <small> Seed A</small>
                                          </td>
                                          <td>
                                            <small> Download</small>
                                          </td>
                                          <td>
                                            <small>September 11th, 2025</small>
                                          </td>
                                        </tr>
                                      )
                                  )
                                ) : (
                                  <tr>
                                    <td
                                      colSpan="3"
                                      style={{ textAlign: "center" }}
                                    >
                                      No result found
                                    </td>
                                  </tr>
                                )}
                              </tbody>
                            </table>
                          </div>
                        </div>
                      </div>
                      <div className="col-12 my-4">
                        <div class="dashboard_card  modern-chart">
                          <div class="card-header">
                            <h3 class="card-title">
                              Recent Activity Signatory
                            </h3>
                          </div>

                          <div class="access-logs">
                            <h4 class="section-title">
                              Company ({CompanyName})
                            </h4>
                            <table className="log-table">
                              <thead>
                                <tr>
                                  <th>Module</th>
                                  <th>Action</th>
                                  <th>Entity Name / Details</th>
                                  <th>IP Address</th>
                                  <th>Date</th>
                                </tr>
                              </thead>
                              <tbody>
                                {lastestactivitySignatory.length > 0 ? (
                                  lastestactivitySignatory.map(
                                    (activity, index) => {
                                      // Safely parse details
                                      let details = activity.details;
                                      if (typeof details === "string") {
                                        try {
                                          details = JSON.parse(details);
                                        } catch (e) {
                                          details = {};
                                        }
                                      }

                                      return (
                                        <tr key={index}>
                                          <td>
                                            <small>{activity.module}</small>
                                          </td>
                                          <td>
                                            <small>{activity.action}</small>
                                          </td>
                                          <td>
                                            <small>
                                              {details?.entity_name ||
                                                details?.company_name ||
                                                details?.document_name ||
                                                "-"}
                                            </small>
                                          </td>
                                          <td>
                                            <small>{activity.ip_address}</small>
                                          </td>
                                          <td>
                                            <small>
                                              {formatCurrentDate(
                                                activity.created_at
                                              )}
                                            </small>
                                          </td>
                                        </tr>
                                      );
                                    }
                                  )
                                ) : (
                                  <tr>
                                    <td
                                      colSpan="5"
                                      style={{ textAlign: "center" }}
                                    >
                                      No result found
                                    </td>
                                  </tr>
                                )}
                              </tbody>
                            </table>
                          </div>
                        </div>
                      </div>
                    </div>
                  </Stepblock>
                </div>
              </SectionWrapper>
            </div>
          </div>
        </div>
      </Wrapper>
    </>
  );
}
